.baseModal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 70%;
    height: 70%;
    background-color: rgb(205, 205, 218);
    border: 2px solid rgb(176, 228, 220);
    border-radius: 2px;
    padding: 4px,;
    flex-direction: column;
}